.title {
  font-size: 50px;
  text-align: center;
  color: #fbd62a;
  text-shadow: 0 -1px 15px black, 0 1px 0 #7f6303, 0 2px 0 #846703,
    0 0 0 #896b03, 0 1px 0 #8e6f03, 0 2px 0 #937203,
    0 2px 30px rgba(252, 223, 92, 0.6);
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.item {
  margin: 15px;
  font-size: 28px;
}

.number {
  color: #fbd62a;
  text-shadow: 0 -1px 15px black, 0 1px 0 #7f6303, 0 2px 0 #846703,
    0 0 0 #896b03, 0 1px 0 #8e6f03, 0 2px 0 #937203,
    0 2px 30px rgba(252, 223, 92, 0.6);
  transition: color;
}
